@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@400;700&family=Work+Sans:wght@400;600&display=swap);
* {
    font-family: 'Work Sans', sans-serif;

    margin: 0;
    padding: 0;

    box-sizing: border-box;
}

.main {
    display: flex;
    justify-content: center;
    align-items: center;
}
.movie__container {
    width: 100%;
    height: 100%;

    padding-top: 40px;

    display: grid;
    grid-template-columns: 50% 50%;
    grid-gap: 30px;
    gap: 30px;
}

.movie__grid {
    width: 100%;
    height: 100%;

    padding: 10px;

    display: grid;
    grid-template-columns: 200px 300px;
    box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.2);

    grid-gap: 10px;

    gap: 10px;
}

.movie__image {
    width: 100%
}

.movie__info__frame {
    display: grid;
    grid-template-rows: 25% 50% 25%;
}

.movie__genres {
    font-size: 12px;

    list-style: none;

    display: flex;
    justify-content: start;
    align-items: flex-end;
}

.movie__genres li {
    margin-right: 2px;
}

@media (max-width: 966px) {
    .movie__container {
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: 320px 320px;
    }
}

@media (max-width: 768px) {
    .movie__container {
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: 320px 320px;
    }

    .movie__grid {
        grid-template-columns: 200px 200px;
        box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.2);
    }
}
.loading__frame {
    width: 100%;
    height: 100vh;

    display: flex;
    justify-content: center;
    align-items: center;
}

.loading__text {
    font-size: 30px;
    font-weight: 700;

    color: #333;
}
